<template>
  <div class="modal modal-application" :class="{ 'modal-application--active': $store.state.modals.applicationFrontText.isActive }">
    <div class="backdrop"></div>
    <div class="content">
      <div class="step step-1-1" v-show="activeClass === 'step-1-1'">
        <h2 class="step__question">Текст?</h2>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="() => setActiveClass('step-1-2')">Да</button>
          <button class="step__btn step__btn--red" @click="() => setActiveClass('step-image-no-text')">Нет</button>
        </div>
      </div>
      <div class="step step-1-2" v-show="activeClass === 'step-1-2'">
        <h2 class="step__question">Шрифт?</h2>
        <ul class="step__list">
          <li class="step__item" v-for="(item, i) in fonts" :key="item.id">
            <input
              class="step__item-radiobtn visuallyhidden"
              :id="`font-${i}`"
              type="radio"
              name="font-group"
              :value="item.font"
              v-model="customFontFamily"
              checked
            >
            <label :for="`font-${i}`" class="step__item-label" :style="item.font">{{ item.labelText }}</label>
          </li>
        </ul>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" :class="{'step__btn--wide': textEditing}" @click="() => setActiveClass('step-1-3')">Далее</button>
          <button class="step__btn step__btn--red" v-if="!textEditing" @click="() => setActiveClass('step-1-1')">Назад</button>
        </div>
      </div>
      <div class="step step-1-3" v-show="activeClass === 'step-1-3'">
        <div class="step__text-wrapper">
          <label class="step__text-input-label" for="text-input">Текст нанесения</label>
          <input
            class="step__text-input"
            id="text-input"
            type="text"
            :style="customFontFamily"
            ref="text-input"
            v-model="text"
            @blur="setApplicationText"
            maxlength="10"
          >
          <p class="step__text-warning">Максимальная длина текста - 10 букв</p>
        </div>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" v-if="!textEditing" @click="() => setActiveClass('step-image')">Далее</button>
          <button class="step__btn step__btn--green" v-if="textEditing" @click="closeModal">Готово</button>
          <button class="step__btn step__btn--red" @click="() => setActiveClass('step-1-2')">Назад</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/main'

export default {
  name: 'ApplicationFrontText',
  data() {
    return {
      textEditing: false,
      activeClass: 'step-1-1',
      customFontFamily: 'font-family: \'Pacifico\', cursive',
      text: '',
      previewBgColor: '#ccc',
      fonts: [
        {
          id: '0001',
          font: "font-family: 'Pacifico', cursive",
          labelText: 'Рукописный'
        },
        {
          id: '0002',
          font: "font-family: 'Bebas Neue', sans-serif; font-weight: 700",
          labelText: 'Базовый прямой'
        },
        {
          id: '0003',
          font: "font-family: 'Bebas Neue', sans-serif; font-weight: 400",
          labelText: 'Тонкий'
        },
        {
          id: '0004',
          font: "font-family: 'Ruslan Display', cursive",
          labelText: 'Готический'
        },
        {
          id: '0005',
          font: "font-family: 'Pattaya', sans-serif",
          labelText: 'Как деньги светы'
        }
      ]
    }
  },
  methods: {
    setActiveClass(newClass) {
      if (newClass === 'step-1-3') {
        this.activeClass = newClass
        this.$refs['text-input'].style.backgroundColor = this.$store.state.clothesLayers.main.fill || this.previewBgColor
      } else if (newClass === 'step-image-no-text') {
        this.$store.commit('setApplicationText', ['', ''])
        this.$store.commit('toggleApplicationModals', 'step-image')
      } else if (newClass === 'step-image') {
        this.$store.commit('toggleApplicationModals', newClass)
      } else {
        this.activeClass = newClass
      }
    },
    setApplicationText() {
      this.$store.commit('setApplicationText', [this.text, this.customFontFamily])
    },
    closeModal() {
      this.textEditing = false
      this.activeClass = 'step-1-1'
      this.$store.commit('toggleApplicationModals')
    }
  },
  created() {
    eventBus.$on('setDefaultClasses', () => {
      this.activeClass = 'step-1-1'
    })
    eventBus.$on('changeModalTextStep', () => {
      this.textEditing = true
      this.setActiveClass('step-1-2')
    })
  }
}
</script>

<style scoped lang="scss">
  @import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Pattaya&family=Ruslan+Display&display=swap');
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  .modal-application--active {
    display: flex;
  }

  .backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 400px;
    padding: 50px;
    text-align: center;
    background-color: #fff;
  }

  .step {
    width: 100%;
  }

  .step__question {
    margin-bottom: 20px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 40px;
  }

  .step__btns-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .step__btn {
    width: 50%;
    padding: 20px 40px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s ease-in;
  }

  .step__btn--green {
    color: #fff;
    background-color: #19a204;

    &:hover {
      background-color: #149203;
    }
  }

  .step__btn--red {
    margin-left: 10px;
    color: #fff;
    background-color: #be2a28;

    &:hover {
      background-color: #a42220;
    }
  }

  .step__btn--wide {
    width: 100%;
  }

  .step__btn:disabled {
    background-color: #aaa;
    cursor: default;
  }

  .step__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 20px;
    list-style: none;
  }

  .step__item-label {
    position: relative;
    display: inline-block;
    margin-bottom: 5px;
    padding-left: 50px;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    cursor: pointer;

    &:hover {
      color: orangered;
    }
  }

  .step__item-label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 30px;
    height: 30px;
    border: 3px solid #000;
  }

  .step__item-label::after {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    display: block;
    width: 18px;
    height: 18px;
    background-color: #000;
    transform: scale(0);
    opacity: 0;
    transition: transform 0.2s ease-in, opacity 0.2s ease-in;
  }

  .step__item-label:hover::before {
    border-color: orangered;
  }

  .step__item-label:hover::after {
    background-color: orangered;
  }

  .step__item-radiobtn:checked + .step__item-label::after {
    transform: scale(1);
    opacity: 1;
  }

  .step__text-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }

  .step__text-input-label {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 30px;
  }

  .step__text-input {
    padding: 30px 20px;
    font-size: 30px;
    line-height: 30px;
    border: 1px solid #aaa;
    background-color: #ccc;
    text-align: center;
  }

  .step__text-warning {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    color: #999;
  }
</style>
