<template>
  <div class="modal modal-application" :class="{ 'modal-application--active': $store.state.modals.applicationFrontImage.isActive }">
    <div class="backdrop"></div>
    <div class="content">
      <div class="step step-1-1" v-show="activeClass === 'step-1-1'">
        <h2 class="step__question">Картинка?</h2>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="() => setActiveClass('step-1-2')">Да</button>
          <button class="step__btn step__btn--red" @click="closeModal('no-image')">Нет</button>
        </div>
      </div>
      <div class="step step-1-2" v-show="activeClass === 'step-1-2'">
        <h2 class="step__question">Какая?</h2>
        <div class="step__preview" ref="preview">
          <img class="step__preview-image" :src="imageSrc" alt="#" v-if="customImage">
          <p class="step__preview-text" :style="applicationTextStyle">{{ applicationText }}</p>
        </div>
        <button class="step__change-text-btn" @click="setActiveClass('step-text')">Изменить текст</button>
        <div class="step__list-wrapper">
          <ul class="step__list grid">
            <li class="step__item" v-for="(item, i) in images" :key="`image-${i+1}`">
              <input
                class="step__item-radiobtn visuallyhidden"
                :id="`image-${i+1}`"
                type="radio"
                name="image-group"
                :value="item.value"
                v-model="customImage"
              >
              <label :for="`image-${i+1}`" class="step__item-label">
                <v-svgicon class-name="step__item-icon" :icon-name="item.iconName" v-if="item.iconName"/>
                <span class="step__item-label-text" :class="{ 'step__item-label-wo-img': !item.iconName }">{{ item.itemText }}</span>
              </label>
            </li>
          </ul>
        </div>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" :class="{'step__btn--wide': imageEditing}" @click="() => setActiveClass('step-1-3')">Далее</button>
          <button class="step__btn step__btn--red" v-if="!imageEditing" @click="() => setActiveClass('step-1-1')">Назад</button>
        </div>
      </div>
      <div class="step step-1-3" v-show="activeClass === 'step-1-3'">
        <h2 class="step__question">Тип нанесения?</h2>
        <ul class="step__list apptypes__list">
          <li class="step__item apptypes__item" v-for="(type, i) in appTypes" :key="`apptype-${i+1}`">
            <input
              class="step__item-radiobtn visuallyhidden"
              :id="`apptype-${i+1}`"
              type="radio"
              name="apptypes-group"
              :value="type"
              v-model="applicationType"
            >
            <label :for="`apptype-${i+1}`" class="step__item-label apptypes__item-label">{{ type }}</label>
          </li>
        </ul>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="() => setActiveClass('step-1-4')">Далее</button>
          <button class="step__btn step__btn--red" @click="() => setActiveClass('step-1-2')">Назад</button>
        </div>
      </div>
      <div class="step step-1-4" v-show="activeClass === 'step-1-4'">
        <h2 class="step__question">Размер картинки?</h2>
        <ul class="step__list apptypes__list">
          <li class="step__item apptypes__item" v-for="(size, i) in imageSizes" :key="`imagesize-${i+1}`">
            <input
              class="step__item-radiobtn visuallyhidden"
              :id="`imagesize-${i+1}`"
              type="radio"
              name="imagesizes-group"
              :value="size"
              v-model="imageSize"
            >
            <label :for="`imagesize-${i+1}`" class="step__item-label apptypes__item-label">{{ size }}</label>
          </li>
        </ul>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="closeModal">Готово</button>
          <button class="step__btn step__btn--red" @click="() => setActiveClass('step-1-3')">Назад</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/main'

export default {
  name: 'ApplicationFrontImage',
  data() {
    return {
      imageEditing: false,
      activeClass: 'step-1-1',
      customImage: '001-rainbow',
      applicationType: 'Вышивка',
      imageSize: '30x30',
      images: [
        {
          value: '001-rainbow',
          iconName: 'images/001-rainbow',
          itemText: 'Радуга'
        },
        {
          value: '002-skull',
          iconName: 'images/002-skull',
          itemText: 'Череп'
        },
        {
          value: '003-heart',
          iconName: 'images/003-heart',
          itemText: 'Сердечко'
        },
        {
          value: '004-pink-cosmos',
          iconName: 'images/004-pink-cosmos',
          itemText: 'Цветочек'
        },
        {
          value: '005-medicine',
          iconName: 'images/005-medicine',
          itemText: 'Таблеточка'
        },
        {
          value: '006-music-note',
          iconName: 'images/006-music-note',
          itemText: 'Нота'
        },
        {
          value: '007-stars',
          iconName: 'images/007-stars',
          itemText: 'Блёстки'
        },
        {
          value: '008-happy',
          iconName: 'images/008-happy',
          itemText: 'Смайлик'
        },
        {
          value: '009-mad',
          iconName: 'images/009-mad',
          itemText: 'Грустный смайлик'
        },
        {
          value: '',
          iconName: '',
          itemText: 'Без картинки'
        }
      ],
      appTypes: ['Вышивка', 'Плёнка', 'Стразы'],
      imageSizes: ['10x10', '20x20', '30x30']
    }
  },
  methods: {
    setActiveClass(newClass) {
      if (newClass === 'step-text') {
        this.$store.commit('toggleApplicationModals', newClass)
      } else if (newClass === 'step-1-2') {
        this.activeClass = newClass
        this.$refs.preview.style.backgroundColor = this.$store.state.clothesLayers.main.fill || '#ccc'
      } else {
        this.activeClass = newClass
      }
    },
    closeModal(data) {
      if (data === 'no-image') {
        this.$store.commit('setApplicationImage', {
          image: '',
          type: '',
          size: ''
        })
      } else {
        this.$store.commit('setApplicationImage', {
          image: this.customImage,
          type: this.applicationType,
          size: this.imageSize
        })
      }
      this.imageEditing = false
      this.$store.commit('toggleApplicationModals')
      this.activeClass = 'step-1-1'
      eventBus.$emit('setDefaultClasses')
    }
  },
  computed: {
    applicationText() {
      return this.$store.state.modals.applicationText.text
    },
    applicationTextStyle() {
      return this.$store.state.modals.applicationText.style
    },
    imageSrc() {
      return this.customImage ? require(`@/assets/images/${this.customImage}.svg`) : '#'
    }
  },
  created() {
    eventBus.$on('changeModalImageStep', () => {
      this.imageEditing = true
      this.setActiveClass('step-1-2')
    })
  }
}
</script>

<style scoped lang="scss">
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  .modal-application--active {
    display: flex;
  }

  .backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 400px;
    padding: 50px;
    text-align: center;
    background-color: #fff;
  }

  .step {
    width: 100%;
  }

  .step__question {
    margin-bottom: 20px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 40px;
  }

  .step__btns-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .step__btn {
    width: 50%;
    padding: 20px 40px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s ease-in;
  }

  .step__btn--green {
    color: #fff;
    background-color: #19a204;

    &:hover {
      background-color: #149203;
    }
  }

  .step__btn--red {
    margin-left: 10px;
    color: #fff;
    background-color: #be2a28;

    &:hover {
      background-color: #a42220;
    }
  }

  .step__btn--wide {
    width: 100%;
  }

  .step__btn:disabled {
    background-color: #aaa;
    cursor: default;
  }

  .step__preview {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 200px;
    min-height: 173px;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #ccc;
  }

  .step__preview-image {
    width: 100px;
    height: 100px;
  }

  .step__preview-text {
    font-size: 30px;
  }

  .step__change-text-btn {
    margin-bottom: 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    text-decoration: underline;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &:hover {
      color: orangered;
    }
  }

  .grid {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 0 10px;
  }

  .step__list {
    margin-bottom: 20px;
    list-style: none;
  }

  .step__item {
    width: 100%;
  }

  .step__item-label {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 5px 10px;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    white-space: nowrap;
    cursor: pointer;

    &:hover {
      background-color: #eee;
    }
  }

  .step__item-label-wo-img {
    padding-left: 30px;
  }

  .step__item-radiobtn:checked + .step__item-label {
    background-color: #d0ffc8;
  }

  .step__item-label-text {
    margin-left: 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
  }

  .apptypes__list {
    text-align: left;
  }

  .apptypes__item-label {
    position: relative;
    display: inline-block;
    width: 100%;
    margin-bottom: 5px;
    padding: 0 0 0 50px;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    cursor: pointer;

    &:hover {
      color: orangered;
      background-color: transparent;
    }
  }

  .apptypes__item-label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 30px;
    height: 30px;
    border: 3px solid #000;
  }

  .apptypes__item-label::after {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    display: block;
    width: 18px;
    height: 18px;
    background-color: #000;
    transform: scale(0);
    opacity: 0;
    transition: transform 0.2s ease-in, opacity 0.2s ease-in;
  }

  .apptypes__item-label:hover::before {
    border-color: orangered;
  }

  .apptypes__item-label:hover::after {
    background-color: orangered;
  }

  .step__item-radiobtn:checked + .apptypes__item-label::after {
    transform: scale(1);
    opacity: 1;
  }

  .step__item-radiobtn:checked + .apptypes__item-label {
    background-color: transparent;
  }

  .step__item-icon {
    width: 30px;
    height: 30px;
  }

  .step__text-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }

  .step__text-input-label {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 30px;
  }

  .step__text-input {
    padding: 30px 20px;
    font-size: 30px;
    line-height: 30px;
    border: 1px solid #aaa;
    background-color: #ccc;
    text-align: center;
  }
</style>
