<template>
  <div :class="['modal', isActive]">
    <div class="backdrop" @click="closeModal"></div>
    <div class="modal__content">
      <p class="modal__text">Вы выбрали максимальное количество цветов (3). Незя больше!</p>
      <button class="btn modal__btn" @click="closeModal">Мау!</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ColorsRestrictionWarning',
  methods: {
    closeModal() {
      this.$store.commit('toggleColorRestriction')
      document.body.classList.remove('blocked')
    }
  },
  computed: {
    isActive() {
      return this.$store.state.modals.colorsRestrictionWarning.isActive ? 'modal--visible' : ''
    }
  }
}
</script>

<style scoped lang="scss">
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    transition: all 0.5s ease-in;
  }

  .modal--visible {
    display: flex;
  }

  .backdrop {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
  }

  .modal__content {
    width: 80%;
    padding: 50px;
    text-align: center;
    background-color: #fff;
  }

  .modal__text + .modal__text {
    margin-top: 10px;
  }

  .modal__btn {
    margin-top: 30px;
  }
</style>
