<template>
  <form class="image-options" name="image-options">
    <h2 class="image-options__heading">Конструктор худи<br/>от мамкупи</h2>
    <div class="image-options__info-block">
      <p class="image-options__info-composition"><span>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Animi commodi, dignissimos dolores dolorum, error eveniet facere labore magni molestias nisi provident similique, sint vitae. Nihil officia repellat sequi soluta voluptates.</span></p>
      <p class="image-options__info-price">3000 руб.</p>
<!--      <a class="image-options__info-delivery" href="#">Бесплатная доставка и возвраты*</a>-->
    </div>
    <div class="hoodie-size">
      <div class="hoodie-size__select-label-wrapper">
        <label class="hoodie-size__select-label" for="hoodie-size">Размер: </label>
        <button class="hoodie-size__btn" type="button" @click="openTableOfSizes">
          <span class="hoodie-size__btn-inner">
            <v-svgicon class-name="hoodie-size__btn-icon" icon-name="icons/ruler" />
            <span class="hoodie-size__btn-text">Таблица размеров</span>
          </span>
        </button>
      </div>
      <select class="hoodie-size__select" name="hoodie-size" id="hoodie-size">
        <option
          v-for="(item, i) in ['xs', 's', 'm', 'l', 'xl', 'xxl']"
          :value="`hoodie-size-${item}`"
          :key="`hoodie-size-item-${i}`"
          :selected="i === 0"
        >
          {{ item.toUpperCase() }}
        </option>
      </select>
    </div>
    <div class="image-options__category">
      <h3 class="image-options__category-heading">Цвета</h3>
      <ul class="colors__list">
        <li class="colors__item" v-for="item in colors" :key="item.colorId">
          <input
            class="colors__item-checkbox visuallyhidden"
            type="radio"
            :disabled="checkDisabled(item.colorId)"
            :id="item.colorId"
            :value="item.color"
            name="colors"
            ref="checkbox"
            :checked="activeLayerColor === item.colorId"
            @change="setActiveLayerColor(item.colorId)"
          >
          <label
            class="colors__item-label"
            :for="item.colorId"
            :style="{backgroundColor: `#${item.colorId}`}"
            @click="warningCheck(item.colorId)"
          >
            {{ item.color }}
          </label>
        </li>
      </ul>
      <p class="colors__checked-list">
        Использовано цветов: {{ pickedColorsAmount }} / {{ maxColors }}
      </p>
    </div>
    <button class="btn image-options__add-btn">Добавить в корзину</button>
  </form>
</template>

<script>
export default {
  name: 'ImageOptions',
  data() {
    return {
      colors: this.$store.state.colors
    }
  },
  methods: {
    setActiveLayerColor(color) {
      this.$store.commit('setActiveLayerColor', color)
      this.markPickedColor()
    },
    markPickedColor() {
      const pickedColors = this.$store.state.pickedColors
      this.$refs.checkbox.forEach(checkbox => {
        if (pickedColors.has(checkbox.id)) {
          checkbox.nextElementSibling.classList.add('colors__item-label--picked')
        } else if (checkbox.nextElementSibling.classList.contains('colors__item-label--picked')) {
          checkbox.nextElementSibling.classList.remove('colors__item-label--picked')
        }
      })
    },
    checkDisabled(colorId) {
      const state = this.$store.state
      if (state.activeLayer) {
        const activeLayerFill = state.clothesLayers[state.activeLayer].fill
        return !activeLayerFill && this.pickedColorsAmount === 3 && !state.pickedColors.has(colorId)
      } else {
        return !state.activeLayer
      }
    },
    openTableOfSizes() {
      this.$store.commit('toggleTableOfSizes')
      document.body.classList.add('blocked')
    },
    warningCheck(color) {
      const pickedColors = new Set(this.$store.state.pickedColors)
      pickedColors.add(color)
      if (pickedColors.size > 3) {
        document.body.classList.add('blocked')
        this.$store.commit('toggleColorRestriction')
      }
    }
  },
  computed: {
    activeLayerColor() {
      const activeLayer = this.$store.state.clothesLayers[this.$store.state.activeLayer]
      if (activeLayer && activeLayer.fill) {
        return activeLayer.fill.slice(1)
      }
      return null
    },
    pickedColorsAmount() {
      return this.$store.state.pickedColors.size
    },
    maxColors() {
      return this.$store.state.maxColors
    }
  }
}
</script>

<style scoped lang="scss">
  .image-options {
    max-width: 400px;
    margin-left: 50px;
  }

  .image-options__heading {
    margin-bottom: 10px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 45px;
    font-weight: 700;
    line-height: 1;
    color: #000;
  }

  .image-options__info-block {
    margin-bottom: 30px;
  }

  .image-options__info-composition {
    margin-bottom: 40px;
    font-size: 18px;
    font-weight: 100;
    color: #828282;
  }

  .image-options__info-price {
    margin-bottom: 10px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 40px;
    color: #000;
    font-weight: 700;
  }

  .image-options__info-delivery {
    display: block;
    margin-bottom: 30px;
    font-size: 16px;
    color: #000;
    font-weight: 100;
  }

  .image-options__category-heading {
    margin-bottom: 20px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 30px;
    font-weight: 500;
    color: #000;
  }

  .colors__list {
    display: grid;
    grid-template-columns: repeat(5, 50px);
    grid-template-rows: 50px 50px;
    grid-gap: 15px;
    margin-bottom: 20px;
    list-style: none;
    font-size: 0;
    line-height: 0;
  }

  .colors__item-label {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    font-size: 0;
    line-height: 0;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.5s ease-in-out;
  }

  .colors__item-checkbox:checked + .colors__item-label {
    background-image: url('../assets/icons/check-mark.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50%;
  }

  .colors__item-label--picked::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 4px;
    height: 4px;
    background-color: #333;
  }

  .colors__checked-list {
    padding-bottom: 30px;
  }

  .hoodie-size {
    margin-bottom: 50px;
  }

  .hoodie-size__select {
    width: 100%;
    min-width: 100px;
    padding: 3px 10px;
    font-size: 20px;
    border: 3px solid #000;
  }

  .hoodie-size__select-label-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }

  .hoodie-size__select-label {
    display: inline-block;
    margin-right: 10px;
    white-space: nowrap;
  }

  .hoodie-size__btn-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0;
    line-height: 0;
  }

  .hoodie-size__btn {
    line-height: 1;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &:hover .hoodie-size__btn-icon {
      fill: #777;
    }

    &:hover .hoodie-size__btn-text {
      color: #777;
    }
  }

  .hoodie-size__btn-icon {
    width: 30px;
    margin-right: 10px;
  }

  .hoodie-size__btn-text {
    font-size: 16px;
    line-height: 1;
    text-decoration: underline;
  }
</style>
