<template>
  <div class="options__wrapper">
    <ul class="options">
      <li class="options__item">
        <input
          class="options__checkbox visuallyhidden"
          id="hood"
          type="checkbox"
          :checked="$store.state.options.hood"
          @change="(e) => changeOptions(e)"
        >
        <label class="options__label" for="hood">Капюшон</label>
      </li>
      <li class="options__item">
        <input
          class="options__checkbox visuallyhidden"
          id="laces"
          type="checkbox"
          :checked="$store.state.options.laces"
          :disabled="!$store.state.options.hood"
          @change="(e) => changeOptions(e)"
        >
        <label class="options__label" for="laces">Шнурки</label>
      </li>
      <li class="options__item">
        <input
          class="options__checkbox visuallyhidden"
          id="pocket"
          type="checkbox"
          :checked="$store.state.options.pocket"
          @change="(e) => changeOptions(e)"
        >
        <label class="options__label" for="pocket">Карман</label>
      </li>
    </ul>
    <ul class="options">
      <li class="options__item">
        <button class="btn btn--white" @click="showModalApplication">Нанесение на груди</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Options',
  data() {
    return {
      application: 'type-vishivka'
    }
  },
  methods: {
    changeOptions(e) {
      const layer = e.target.id
      this.$store.commit('changeOptions', layer)
    },
    showModalApplication() {
      this.$store.commit('showModalApplication')
    }
  }
}
</script>

<style scoped lang="scss">
  .options__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .options {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    list-style: none;
  }

  .options__item {
    display: flex;
    align-items: center;
    padding: 5px 0;
  }

  .options__label {
    position: relative;
    display: inline-block;
    margin-bottom: 5px;
    padding-left: 50px;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    cursor: pointer;
  }

  .options__label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 30px;
    height: 30px;
    border: 3px solid #000;
  }

  .options__label::after {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    display: block;
    width: 18px;
    height: 18px;
    background-color: #000;
    transform: scale(0);
    opacity: 0;
    transition: all 0.2s ease-in;
  }

  .options__checkbox:checked + .options__label::after {
    transform: scale(1);
    opacity: 1;
  }

  .options__checkbox:disabled + .options__label {
    color: #aaa;
  }

  .options__checkbox:disabled + .options__label::before {
    border-color: #aaa;
  }

  .options__checkbox:disabled + .options__label::after {
    background-color: #aaa;
  }

  .options__select {
    width: 100%;
    min-width: 100px;
    padding: 3px 10px;
    font-size: 20px;
    border: 3px solid #000;
  }

  .options__select-label {
    margin-right: 10px;
    white-space: nowrap;
  }
</style>
