<script>
export default {
  name: 'TableOfSizes',
  methods: {
    closeModal() {
      this.$store.commit('toggleTableOfSizes')
      document.body.classList.remove('blocked')
    }
  },
  updated() {
    const sizeEdges = document.documentElement.clientHeight
    if (this.$refs.content.offsetHeight > sizeEdges) {
      this.$refs.inner.style.alignItems = 'flex-start'
    }
  }
}
</script>

